body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: Arial, sans-serif;
  text-align: center;
  background-color: #f4f4f9;
  margin: 0;
  padding: 20px;
  font-family: 'Times New Roman', Times, serif;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 80%;
  margin: auto;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  padding: 20px;

}

.img-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  height: 300px;
  background-color: #ccc;
  border-radius: 13px;
  padding: 10px;
  padding-left: 0px;
}


h1 {
  color: #333;
  font-family: 'Times New Roman', Times, serif;
}

.candidate {
  display: flex;
  justify-content: column;
  display: inline-block;
  margin: 20px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  width: 25%;
  background: #f9f9f9;
}

.candidate img {
  width: 120px;
  height: 120px;
  border-radius: 100%;
  margin-bottom: 10px;
  object-fit: cover;
}

button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #28a745;
  color: white;
  font-size: 16px;
  cursor: pointer;
}

button:hover {
  background-color: #218838;
}

p {
  margin: 10px 0;
}